.worked__body {
  display: flex;
  column-gap: 32px;
  row-gap: 16px;
}
@media (max-width: 1025px) {
  .worked__body {
    flex-direction: column;
  }
}
@media (max-width: 768px) {
  .worked__body {
    gap: 35px;
  }
}

.worked__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 164px;
}
@media (max-width: 1025px) {
  .worked__container {
    gap: 116px;
  }
}
@media (max-width: 992.99px) {
  .worked__container {
    flex-direction: column;
    gap: 48px;
  }
}

.worked__title {
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: 0px;
  max-width: 650px;
}
@media (max-width: 992.99px) {
  .worked__title {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .worked__title {
    font-size: 32px;
    line-height: 40px;
    text-align: start;
  }
}

.worked__statistic-percent {
  font-size: 40px;
  line-height: 48px;
  font-weight: 700;
  font-family: "Sora", sans-serif;
  padding-block-end: 16px;
}
@media (max-width: 768px) {
  .worked__statistic-percent {
    font-size: 32px;
    line-height: 40px;
  }
}

.worked__statistic-text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
}